<template lang="html">
  <div class="availability__list__each">
    <div class="availability__list__readonly" v-if="!editMonday">
      <div class="availability__list__day">
        <span>Monday</span>
      </div>
      <div class="availability__list__time" v-for="(time, i) in dataMonday">
        <span>{{ time.start }} to {{ time.end }}</span>
      </div>
      <div class="availability__list__add">
        <span class="semi__btn" @click="editScheduleMonday">Edit</span>
      </div>
    </div>
    <div class="availability__list__edit" v-if="editMonday">
      <div class="availability__list__left">
        <div class="availability__list__day">
          <span>Monday</span>
        </div>
        <div class="availability__list__time--edit">
          <div class="availability__list__time--picker" v-for="(time, i) in dataMonday">
            <VueCtkDateTimePicker
              v-model="time.start"
              id="TimePicker"
              label="Select time"
              color="#2B89B9"
              buttonColor="#2B89B9"
              only-time
              format="HH:mm"
              formatted="HH:mm"
              input-size="sm"
              minute-interval="30"
            />
            to
            <VueCtkDateTimePicker
              v-model="time.end"
              id="TimePicker"
              label="Select time"
              color="#2B89B9"
              buttonColor="#2B89B9"
              only-time
              format="HH:mm"
              formatted="HH:mm"
              input-size="sm"
              minute-interval="30"
            />
            <div class="flex--center width--30perc">
              <span
                class="semi__btn--red flex--center"
                @click="postRemove(i, time.id)"
              >
                <CrossIcon
                  width="14px"
                  height="14px"
                  color="#ED5285"
                />
              </span>
              |
              <span
                class="semi__btn flex--center"
                @click="postEdit(i, time.id, time.start, time.end)"
              >
                <CheckIcon
                  width="14px"
                  height="14px"
                  color="#90E2DA"
                />
              </span>
            </div>
          </div>
        </div>
        <div class="availability__list__time--edit" v-for="(timepicker, i) in timepickers" :key="i">
          <div class="availability__list__time--picker">
            <VueCtkDateTimePicker
              v-model="time_start"
              id="TimePicker"
              label="Select time"
              color="#2B89B9"
              buttonColor="#2B89B9"
              only-time
              format="HH:mm"
              formatted="HH:mm"
              input-size="sm"
              minute-interval="30"
            />
            to
            <VueCtkDateTimePicker
              v-model="time_end"
              id="TimePicker"
              label="Select time"
              color="#2B89B9"
              buttonColor="#2B89B9"
              only-time
              format="HH:mm"
              formatted="HH:mm"
              input-size="sm"
              minute-interval="30"
            />
            <div class="flex--center width--30perc">
              <span
                class="semi__btn--red flex--center"
                @click="postRemove(i, time.id)"
              >
                <CrossIcon
                  width="14px"
                  height="14px"
                  color="#ED5285"
                />
              </span>
              |
              <span
                class="semi__btn flex--center"
                @click="updateSchedule"
              >
                <CheckIcon
                  width="14px"
                  height="14px"
                  color="#90E2DA"
                />
              </span>
            </div>
          </div>
        </div>
        <div class="availability__list__add" v-if="showAddmore">
          <span class="semi__btn" @click="addSchedule">+ add more</span>
        </div>
      </div>
      <div class="availability__list__right">
        <v-btn
          depressed
          small
          color="#375197"
          style="color:#fff;"
          @click="cancelEdit">
          Close
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker';
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.css';
import axios from '../../../store/axios-auth'
import CheckIcon from '../../../components/icons/CheckIcon.vue'
import CrossIcon from '../../../components/icons/CrossIcon.vue'

export default {
  components: {
    VueCtkDateTimePicker,
    CheckIcon,
    CrossIcon
  },
  props: ['dataMonday'],
  data () {
    return {
      editMonday: false,
      timepickersMax: 4,
      timepickers: [],
      time_start: null,
      time_end: null,
      showAddmore: true
    }
  },
  methods: {
    editScheduleMonday () {
      this.editMonday = !this.editMonday;
      if ((this.timepickers.length + this.dataMonday.length) < this.timepickersMax ) {
        this.showAddmore = true
      } else if ((this.timepickers.length + this.dataMonday.length) == this.timepickersMax) {
        this.showAddmore = false
      }
    },
    cancelEdit () {
      this.editMonday = !this.editMonday;
      this.timepickers = []
      this.$emit('child-event', 'clicked');
    },
    addSchedule () {
      if (this.timepickers.length == (this.timepickersMax - this.dataMonday.length)) {
      	return
      }
      if (((this.timepickers.length + 1) + this.dataMonday.length) < this.timepickersMax ) {
        this.showAddmore = true
      } else if (((this.timepickers.length + 1) + this.dataMonday.length) == this.timepickersMax) {
        this.showAddmore = false
      }
      const newPicker = {}
    	this.timepickers.push(newPicker)
    },
    updateSchedule () {
      const dataSchedule = {
        day: 'monday',
        start_time: this.time_start,
        end_time: this.time_end
      }
      this.$store.dispatch('POST_SCHEDULE', dataSchedule)
      // this.$emit('child-snackbar', 'true');
    },
    postEdit(i, id, start_time, end_time) {
      const rescheduleDetail = {
        start_time : start_time,
        end_time : end_time
      }
      // console.log(rescheduleDetail);
      axios({
        method: 'put',
        url: `/schedules/${id}`,
        data: rescheduleDetail,
        headers: {Authorization: localStorage.getItem('token')}
       })
      .then(res => {
        console.log(res);
        const resData = res.data
        this.$store.commit("commitSnackbarMessage", resData)
        this.$store.commit('commitSnackbarVal', true)
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    postRemove(i, id) {
      axios({
        method: 'delete',
        url: `/schedules/${id}`,
        headers: {Authorization: localStorage.getItem('token')}
       })
      .then(res => {
        console.log(res);
        const resData = res.data
        this.$store.commit("commitSnackbarMessage", resData)
        this.$store.commit('commitSnackbarVal', true)
      })
      .catch(function (error) {
        console.log(error);
      });
      this.dataMonday.splice(i, 1)
      if ((this.timepickers.length + this.dataMonday.length) < this.timepickersMax ) {
        this.showAddmore = true
      } else if ((this.timepickers.length + this.dataMonday.length) == this.timepickersMax) {
        this.showAddmore = false
      }
    }
  },
  computed: {
    coachScheduleUpdate () {
      const coachUpdate = this.$store.getters.COACH_SCHEDULES_UPDATE

      if (coachUpdate == true) {
        return this.$emit('child-event', 'clicked');
        return this.$forceUpdate();
        return this.timepickers = []
      }
    }
  }
}
</script>

<style lang="css">
</style>



