<template lang="html">
  <div class="table__list">
    <table style="width:100%">
      <tr>
        <th>Date</th>
        <th>Remark / Description</th>
        <th>Status</th>
        <th>Action</th>
      </tr>
      <tr
        v-for="(data, i) in listDayoff"
        :key="i"
      >
        <td align="center">{{data.start_time | formatDate}} - {{data.end_time | formatDate}}</td>
        <td align="center">{{data.remark}}</td>
        <td align="center">{{data.status}}</td>
        <td style="display:flex;justify-content:center;align-items:center;color:#9ba8cb;" v-if="data.status == 'requested'">
          <div class="semi__btn--red" @click="changeDialogStateTwo(i, data.id, data.start, data.end, data.remark)">Cancel</div> |
          <div class="semi__btn--blue" @click="changeDialogState(i, data.id, data.remark)">Update</div>
        </td>
        <td align="center" style="color:#9ba8cb;font-size:1.5em;" v-if="data.status == 'cancelled' || data.status == 'approved'">-</td>
      </tr>
    </table>

    <!-- UPDATE DIALOG -->
    <v-dialog width="400" v-model="showDialog" persistent>
       <v-card style="background-color:#fff;border-radius: 3px;">
        <div class="view__header__title">
         <div>
           <h2>Edit Request</h2>
           <div class="blue__circle"></div>
         </div>
        </div>
        <div class="dialog__datepicker">
          <label>Pick dates</label>
          <div class="modal__datepicker">
            <label>From</label>
            <div class="datepicker--border">
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="start_time"
                  append-icon="event"
                  @click="togleDatepicker"
                ></v-text-field>
                <v-date-picker
                  v-model="start_time"
                  v-if="menu"
                  no-title
                  :min="minDate"
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="menu = false">OK</v-btn>
                </v-date-picker>
              </v-menu>
            </div>
            <label>To</label>
            <div class="datepicker--border">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="end_time"
                  append-icon="event"
                  @click="togleDatepicker2"
                ></v-text-field>
                <v-date-picker
                  v-model="end_time"
                  v-if="menu2"
                  no-title
                  :min="start_time"
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
                </v-date-picker>
              </v-menu>
            </div>
          </div>
        </div>
        <div class="dialog__remark">
          <label>Remark</label>
          <textarea name="name" rows="6" cols="40" v-model="newRemark"></textarea>
        </div>
        <div class="reschedule__btn">
          <div
            style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
            @click="changeDialogState">
            Cancel
          </div>
          <v-btn
            depressed
            small
            color="#375197"
            style="color:#fff;"
            @click="putUpdateDayoff">
            Update
          </v-btn>
        </div>
      </v-card>
    </v-dialog>

    <!-- CANCEL DIALOG -->
    <v-dialog width="400" v-model="showDialogTwo" persistent>
       <v-card style="background-color:#fff;border-radius: 3px;">
        <div class="dialog__remark">
          <span>Are you sure want to cancel this day-off request?</span>
        </div>
        <div class="reschedule__btn">
          <div
            style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
            @click="changeDialogStateTwo">
            Close
          </div>
          <v-btn
            depressed
            small
            color="#375197"
            style="color:#fff;"
            @click="putCancelDayoff">
            Yes
          </v-btn>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'
import { DateRange } from 'vuetify-daterange-picker';
import 'vuetify-daterange-picker/dist/vuetify-daterange-picker.css';
import { format, subDays, addDays } from 'date-fns';
import moment from 'moment';

export default {
  mixins: [MomentFilter],
  components: {
    [DateRange.name]: DateRange
  },
  data () {
    return {
      minDate: new Date().toISOString().substr(0, 10),
      menu: false,
      menu2: false,
      start_time: null,
      end_time: null,
      dateMenu: false,
      newRemark: null,
      timeout: 3000,
      updateData: null,
      cancelData: null,
    }
  },
  created () {
    this.$store.dispatch("GET_DAYOFF")
  },
  computed: {
    listDayoff () {
      function compare(a, b) {
        if (a.id < b.id)
          return 1;
        if (a.id > b.id)
          return -1;
        return 0;
      }

      const getDayoff = this.$store.state.stateGetDayoff
      if (getDayoff == null) {
        return 0
      } else {
        return getDayoff.data.sort(compare);
      }
    },
    showDialog () {
      return this.$store.state.showDialog
    },
    showDialogTwo () {
      return this.$store.state.showDialogTwo
    },
  },
  methods: {
    changeDialogState (i, id) {
      const data = id
      // console.log(data);
      this.updateData = data
      this.$store.commit('toggleDialog');
    },
    changeDialogStateTwo (i, id, start, end, remark) {
      const start_time = moment(start).format('YYYY-MM-DD')
      const end_time = moment(end).format('YYYY-MM-DD')
      const data = {
        id: id,
        start_time: start_time,
        end_time: end_time,
        remark: remark
      }
      // console.log(data);
      this.cancelData = data
      this.$store.commit('toggleDialogTwo');
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    },
    putUpdateDayoff () {
      const updateData = this.updateData
      const data = {
        id: updateData,
        start_time: this.start_time,
        end_time: this.end_time,
        remark: this.newRemark
      }
      // console.log(data);
      this.$store.dispatch("PUT_UPDATE_DAYOFF", data)
      this.$store.commit('toggleDialog');
    },
    putCancelDayoff () {
      const cancelData = this.cancelData
      this.$store.dispatch("PUT_CANCEL_DAYOFF", cancelData)
      this.$store.commit('toggleDialogTwo');
    },
    onDateRangeChange(range) {
      this.range = range;
    }
  },
  watch: {

  }
}
</script>

<style lang="scss" scoped>
.v-dialog {

  & > .v-card {
    padding: 20px 50px;
  }
}
.menuable__content__active {

  &> .theme--light.v-card {
    padding: 0 !important;
  }
}
.reschedule__btn {
  margin: 5px 0;
  display: flex;
  justify-content: flex-end;

  > button {
    margin: 5px 0;
  }
}
</style>



