<template lang="html">
  <v-dialog width="400" v-model="showDialog" persistent>
     <v-card style="background-color:#fff;border-radius: 3px;">
      <div class="dialog__remark">
        <span>Are you sure want to cancel this day-off request?</span>
      </div>
      <div class="reschedule__btn">
        <div
          style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
          @click="changeDialogState">
          Close
        </div>
        <v-btn
          depressed
          small
          color="#375197"
          style="color:#fff;"
          @click="">
          Yes
        </v-btn>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  components: {

  },
  data () {
    return {
      menu: false,
      dateMenu: false,
    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialogTwo
    }
  },
  methods: {
    changeDialogState () {
      this.$store.commit('toggleDialogTwo');
    },
  }
}
</script>

<style lang="scss" scoped>
  .v-dialog {

    & > .v-card {
      padding: 20px 50px;
    }
  }
  .menuable__content__active {

    &> .theme--light.v-card {
      padding: 0 !important;
    }
  }
  .reschedule__btn {
    margin: 5px 0;
    display: flex;
    justify-content: flex-end;

    > button {
      margin: 5px 0;
    }
  }
</style>



