<template lang="html">
  <v-dialog width="400" v-model="showDialog" persistent>
     <v-card style="background-color:#fff;border-radius: 3px;">
      <div class="view__header__title">
       <div>
         <h2>Edit Request</h2>
         <div class="blue__circle"></div>
       </div>
      </div>
      <div class="dialog__datepicker">
        <label>Pick dates</label>
        <v-menu full-width offset-y :close-on-content-click="false" v-model="dateMenu" bottom>
          <v-btn color="primary" outline slot="activator" style="width:100%;margin:0 0 10px;border-color:#E0DFDF">{{ range[0] }} &mdash; {{ range[1] }}</v-btn>
          <v-card>
            <v-card-text>
              <v-daterange :options="dateRangeOptionsWithMinDate" @input="onDateRangeChange" no-presets />
            </v-card-text>
          </v-card>
        </v-menu>
      </div>
      <div class="dialog__remark">
        <label>Remark</label>
        <textarea name="name" rows="6" cols="40"></textarea>
      </div>
      <div class="reschedule__btn">
        <div
          style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
          @click="changeDialogState">
          Cancel
        </div>
        <v-btn
          depressed
          small
          color="#375197"
          style="color:#fff;"
          @click="">
          Update
        </v-btn>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
import { DateRange } from 'vuetify-daterange-picker';
import 'vuetify-daterange-picker/dist/vuetify-daterange-picker.css';
import { format, subDays, addDays } from 'date-fns';

export default {
  components: {
    [DateRange.name]: DateRange
  },
  data () {
    return {
      menu: false,
      range: [],
      dateRangeOptionsWithMinDate: {
        startDate: format(new Date(), 'YYYY-MM-DD'),
        endDate: format(new Date(), 'YYYY-MM-DD'),
        minDate: format(subDays(new Date(), 15), 'YYYY-MM-DD'),
        maxDate: format(addDays(new Date(), 20), 'YYYY-MM-DD'),
        format: 'MM/DD/YYYY'
      },
      dateMenu: false
    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    }
  },
  methods: {
    changeDialogState () {
      this.$store.commit('toggleDialog');
    },
    onDateRangeChange(range) {
      this.range = range;
    }
  }
}
</script>

<style lang="scss" scoped>
  .v-dialog {

    & > .v-card {
      padding: 20px 50px;
    }
  }
  .menuable__content__active {

    &> .theme--light.v-card {
      padding: 0 !important;
    }
  }
  .reschedule__btn {
    margin: 5px 0;
    display: flex;
    justify-content: flex-end;

    > button {
      margin: 5px 0;
    }
  }
</style>



