<template lang="html">
  <div class="flex">
    <div class="availability__list__time--picker">
      <VueCtkDateTimePicker
        v-model="time_start"
        id="TimePicker"
        label="Select time"
        color="#2B89B9"
        buttonColor="#2B89B9"
        only-time=true
        format="HH:mm"
        formatted="HH:mm"
        input-size="sm"
        minute-interval="30"
      />
      to
      <VueCtkDateTimePicker
        v-model="time_end"
        id="TimePicker"
        label="Select time"
        color="#2B89B9"
        buttonColor="#2B89B9"
        only-time=true
        format="HH:mm"
        formatted="HH:mm"
        input-size="sm"
        minute-interval="30"
      />
    </div>
  </div>
</template>

<script>
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker';
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.css';
export default {
  components: {
    VueCtkDateTimePicker
  },
  data () {
    return {
      time_start: '00:00',
      time_end: '00:00'
    }
  },
  computed: {
    timeValue () {
      const timeStart = this.time_start
      const timeEnd = this.time_end

      this.$emit('startTime', this.time_start);
      this.$emit('startEnd', timeEnd);

      console.log(this.time_start);
    }
  }
}
</script>

<style lang="scss">
  .flex {
    display: flex;
  }
</style>



