<template lang="html">
  <div class="availability">
    <div class="boxNotif">
       <v-snackbar
        v-model="handleSnackbarVal"
        :color="notifColor"
        :timeout="timeout"
        top
      >
        <p style="padding:0 10px">{{ notifResMessage.message }}</p>
      </v-snackbar>
    </div>
    <div class="availability__title">
      <div class="availability__title__left">
        <div class="view__header__title">
          <div>
            <h2>Schedules</h2>
            <div class="blue__circle"></div>
          </div>
        </div>
        <div class="view__header__disclaimer">
          <h5>Set your availability</h5>
          <h5>You will be available for total 70hours / week</h5>
        </div>
      </div>
      <div class="availability__title__right">
        <div class="view__header__timezone">
          <h5>Timezone {{GET_AdditionaLinfo.gmt}}</h5>
        </div>
      </div>
    </div>

    <div class="availability__list">
      <CoachSchedulesMonday :dataMonday="dataMonday" @child-event="reloadComponent" />
      <CoachSchedulesTuesday :dataTuesday="dataTuesday" @child-event="reloadComponent" />
      <CoachSchedulesWednesday :dataWednesday="dataWednesday" @child-event="reloadComponent" />
      <CoachSchedulesThursday :dataThursday="dataThursday" @child-event="reloadComponent" />
      <CoachSchedulesFriday :dataFriday="dataFriday" @child-event="reloadComponent" />
      <CoachSchedulesSaturday :dataSaturday="dataSaturday" @child-event="reloadComponent" />
      <CoachSchedulesSunday :dataSunday="dataSunday" @child-event="reloadComponent" />
    </div>
  </div>
</template>

<script>
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker';
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.css';
import axios from '../../store/axios-auth'
import Timepicker from './TimepickerComponents/Timepicker'
import TimepickerAdd from './TimepickerComponents/TimepickerAdd'
import CoachSchedulesMonday from './CoachSchedulesDays/CoachSchedulesMonday'
import CoachSchedulesTuesday from './CoachSchedulesDays/CoachSchedulesTuesday'
import CoachSchedulesWednesday from './CoachSchedulesDays/CoachSchedulesWednesday'
import CoachSchedulesThursday from './CoachSchedulesDays/CoachSchedulesThursday'
import CoachSchedulesFriday from './CoachSchedulesDays/CoachSchedulesFriday'
import CoachSchedulesSaturday from './CoachSchedulesDays/CoachSchedulesSaturday'
import CoachSchedulesSunday from './CoachSchedulesDays/CoachSchedulesSunday'

export default {
  components: {
    VueCtkDateTimePicker,
    Timepicker,
    TimepickerAdd,
    CoachSchedulesMonday,
    CoachSchedulesTuesday,
    CoachSchedulesWednesday,
    CoachSchedulesThursday,
    CoachSchedulesFriday,
    CoachSchedulesSaturday,
    CoachSchedulesSunday
  },
  data () {
    return {
      inputs:[],
      loader: null,
      loading: false,
      dataMonday: null,
      dataTuesday: null,
      dataWednesday: null,
      dataThursday: null,
      dataFriday: null,
      dataSaturday: null,
      dataSunday: null,
      timepickers: [],
      snackbar: false,
      snackbarData: false,
      timeout: 3000,
    }
  },
  mounted () {
    axios({
      method: 'get',
      url: '/schedules/',
      headers: {Authorization: localStorage.getItem('token')}
     })
     .then(res => {
       console.log(res);
       // response for each day
       let Monday = res.data.data.filter(x => x.day === 'Monday').map(x => x.Monday)
       this.dataMonday = Monday
       let Tuesday = res.data.data.filter(x => x.day === 'Tuesday').map(x => x.Tuesday)
       this.dataTuesday = Tuesday
       let Wednesday = res.data.data.filter(x => x.day === 'Wednesday').map(x => x.Wednesday)
       this.dataWednesday = Wednesday
       let Thursday = res.data.data.filter(x => x.day === 'Thursday').map(x => x.Thursday)
       this.dataThursday = Thursday
       let Friday = res.data.data.filter(x => x.day === 'Friday').map(x => x.Friday)
       this.dataFriday = Friday
       let Saturday = res.data.data.filter(x => x.day === 'Saturday').map(x => x.Saturday)
       this.dataSaturday = Saturday
       let Sunday = res.data.data.filter(x => x.day === 'Sunday').map(x => x.Sunday)
       this.dataSunday = Sunday
       // response for each day end
       // console.log(this.dataTuesday);
     })
     .catch(function (error) {
       console.log(error);
     });
  },
  methods: {
    reloadComponent (status) {
      if (status == "clicked") {
        axios({
          method: 'get',
          url: '/schedules/',
          headers: {Authorization: localStorage.getItem('token')}
         })
         .then(res => {
           // response for each day
           let Monday = res.data.data.filter(x => x.day === 'Monday').map(x => x.Monday)
           this.dataMonday = Monday
           let Tuesday = res.data.data.filter(x => x.day === 'Tuesday').map(x => x.Tuesday)
           this.dataTuesday = Tuesday
           let Wednesday = res.data.data.filter(x => x.day === 'Wednesday').map(x => x.Wednesday)
           this.dataWednesday = Wednesday
           let Thursday = res.data.data.filter(x => x.day === 'Thursday').map(x => x.Thursday)
           this.dataThursday = Thursday
           let Friday = res.data.data.filter(x => x.day === 'Friday').map(x => x.Friday)
           this.dataFriday = Friday
           let Saturday = res.data.data.filter(x => x.day === 'Saturday').map(x => x.Saturday)
           this.dataSaturday = Saturday
           let Sunday = res.data.data.filter(x => x.day === 'Sunday').map(x => x.Sunday)
           this.dataSunday = Sunday
           // response for each day end
           // console.log(this.dataTuesday);
         })
         .catch(function (error) {
           console.log(error);
         });
      }
    },
  },
  computed: {
    GET_AdditionaLinfo(){
			return this.$store.state.DataAdditional
    },
    handleSnackbarVal () {
      return this.$store.state.stateSnackbarVal
    },
    notifResMessage() {
      const message = this.$store.state.stateSnackbarMessage
      if (message == null) {
        return "0"
      } else if (message !== null) {
        return message
      }
      // console.log(resCoins);
    },
    notifColor() {
      const resStatus = this.$store.state.stateSnackbarMessage

      if (resStatus == null) {
        return "none"
      } else if (resStatus.message == "success") {
        return "success"
      } {
        return "error"
      }
    }
  },
  watch: {
    handleSnackbarVal (val) {
      const stateSnackbarVal = false

      if (val == true) {
        setTimeout(() => (
          this.$store.commit("commitSnackbarVal", stateSnackbarVal)
        ), 3000)
      }
      // console.log(val);
    },
    loader () {
      const l = this.loader
      this[l] = !this[l]
      setTimeout(() => (this[l] = false), 3000)
      this.loader = null
    }
  }
}
</script>

<style lang="scss">
  .date-time-picker {
    width: 50%;
  }
  .field {
    display: flex;
    align-items: center;
    justify-content: center;
    &-label {
      left: auto !important;
    }
    &-input {
      text-align: center;
    }
  }
</style>



