<template lang="html">
  <div class="schedule__request">
    <div class="bg--grey">
      <div class="view__header__title">
        <div>
          <h2>Request Days Off</h2>
          <div class="blue__circle"></div>
        </div>
      </div>
      <div class="dialog__datepicker">
        <div class="modal__datepicker">
          <label>From</label>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="start_time"
                append-icon="event"
                @click="togleDatepicker"
              ></v-text-field>
              <v-date-picker
                v-model="start_time"
                v-if="menu"
                no-title
                :min="minDate"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <label>To</label>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="end_time"
                append-icon="event"
                @click="togleDatepicker2"
              ></v-text-field>
              <v-date-picker
                v-model="end_time"
                v-if="menu2"
                no-title
                :min="start_time"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
        </div>
      </div>
      <div class="dialog__remark">
        <label>Remark</label>
        <textarea name="name" rows="6" cols="40" v-model="remark"></textarea>
      </div>
      <div class="schedule__request__btn">
        <v-btn
          depressed
          :loading="loading"
          :disabled="loading"
          small
          color="#375197"
          style="color:#fff;"
          @click="changeDialog">
          Request Days Off
        </v-btn>
      </div>
    </div>
    <v-dialog width="320" v-model="showDialogDayoff" persistent>
       <v-card style="background-color:#fff;border-radius:3px;padding:30px;">
         <div class="flex--col--center dialog__content">
           <span>Are you sure want to request day off?</span>
           <div class="modal__action--btn">
             <div
               style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
               @click="changeDialog">
               Cancel
             </div>
             <v-btn
               depressed
               small
               color="#375197"
               style="color:#fff;"
               @click="postDayOff">
               Confirm
             </v-btn>
           </div>
         </div>
       </v-card>
     </v-dialog>
  </div>
</template>

<script>
import { DateRange } from 'vuetify-daterange-picker';
import 'vuetify-daterange-picker/dist/vuetify-daterange-picker.css';
import { format, subDays, addDays } from 'date-fns';

export default {
  components: {
    [DateRange.name]: DateRange
  },
  data () {
    return {
      remark: "",
      menu: false,
      menu2: false,
      minDate: new Date().toISOString().substr(0, 10),
      start_time: null,
      end_time: null,
      loader: null,
      loading: false,
      showDialogDayoff: false,
      snackbar: false,
      timeout: 3000,
    }
  },
  methods: {
    changeDialog () {
      this.showDialogDayoff = !this.showDialogDayoff
    },
    postDayOff () {
      const dataDayoff = {
        start_time: this.start_time,
        end_time: this.end_time,
        remark: this.remark
      }
      this.$store.dispatch('POST_REQ_DAYOFF', dataDayoff);
      console.log(dataDayoff);
      this.showDialogDayoff = !this.showDialogDayoff
      this.start_time = null
      this.end_time = null
      this.remark = ""
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    }
  },
  computed: {
    userData () {
      return this.$store.state.profile
    },
  },
  watch: {

  }
}
</script>

<style lang="scss">
  .schedule {

    &__request {
      padding: 10px;
      display: flex;
      // justify-content: center;
      align-items: center;
      flex-direction: column;

      &__btn {

        button {
          width: 100%;
          margin: 15px 0 10px;
        }
      }
    }
  }
  .bg--grey {
    width: 100%;
    margin: 5px 0 0;
    padding: 5px 10px;
    background-color: $almostwhite;
    border-radius: 2px;

    h4 {
      color: $fontBlack;
      font-weight: 600;
    }
  }
</style>



